import './Busqueda.css'

import { FirebaseAnalytics } from '@capacitor-community/firebase-analytics'
import { Capacitor } from '@capacitor/core'
import { IonAlert, IonSpinner } from '@ionic/react'
import withStyles from '@material-ui/core/styles/withStyles'
import { Rating } from '@material-ui/lab'
import { observer } from 'mobx-react-lite'
import { cast } from 'mobx-state-tree'
import React, { useEffect, useRef, useState } from 'react'
import WhatsAppIcon from '@material-ui/icons/WhatsApp'
import * as FIcon from 'react-feather'

import { Page } from '../../components'
import { RUTAS } from '../../constants'
import {DetailViewProps, useToaster} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import ProductoCard from '../../components/ProductoCard'

const numeral = require('numeral')

const StyledRating = withStyles({
  iconFilled: {
    color: '#FFA300',
  },
  iconHover: {
    color: '#fc4c02',
  },
})(Rating)


export const Busqueda: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { buscarStore, loginStore } = rootStore
  const Content = useRef<HTMLIonContentElement | null>(null)
  const [Filtros, setFiltros] = useState({} as any)
  const [FiltroRangoPrecio, setFiltroRangoPrecio] = useState(false)
  const [Pagina, setPagina] = useState(1)
  const [CantidadPaginas, setCantidadPaginas] = useState(1)
  const [Cantidad, setCantidad] = useState(-1)
  const [Loading, setLoading] = useState(true)
  const [Ready, setReady] = useState(false)
  const addToast = useToaster()
  const [AlertBloqueo, setAlertBloqueo] = useState<boolean>(false)
  const [Productos, setProductos] = useState([] as any)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const [ShowSideMenu, setShowSideMenu] = useState(!Movil)

  const cantidadPorPagina = 20

  const setFiltro = (clave: string, valor: any) => {
    const filtros = {...Filtros}
    filtros[clave] = valor
    setFiltros(filtros)
  }

  const setup = async () => {
    await rootStore.actualizarDatosInicio(false)
    // Si viene una empresa seleccionada se setea esta en la busqueda y luego se limpia el store
    if (buscarStore.empresa !== 0) {
      setFiltro('empresa', [buscarStore.empresa])
      buscarStore.setEmpresaBusqueda(0)
    }
    if (buscarStore.rubro !== 0) {
      setFiltro('rubro', [buscarStore.rubro])
      buscarStore.setRubroBusqueda(0)
    }
    //
    setTimeout(() => setReady(true), 500)
  }

  useEffect(() => {
    // Se ejecuta únicamente la primera vez
    setup()
  }, [])

  const registroEventoFirebase = (encontrado: boolean) => {
    const data = {
      page: 'busqueda',
      texto_busqueda: Filtros.texto,
      encontrado: encontrado,
    } as any
    if (Capacitor.getPlatform() === 'web') {
      const dataLayer = (window as any).dataLayer || []
      // @ts-ignore
      function gtag() {
        dataLayer.push(arguments)
      }
      if (loginStore.isLoggedIn) {
        data.usuario_id = loginStore.id
      }
      // @ts-ignore
      gtag('event', 'search_item', data)
    } else {
      FirebaseAnalytics.logEvent({
        name: 'page_view',
        params: data,
      })
        .then((res: any) => {
          console.log(res)
        })
        .catch((error: any) => {
          console.error(error)
        })
    }
  }

  const obtenerProductos = (AgregarPagina: boolean, filtros: any = null) => {
    if (!filtros) {
      filtros = Filtros
    }

    setLoading(true)
    setCantidad(-1)

    let precioMinimo = ''
    let precioMaximo = ''
    if (filtros.minimo) {
      precioMinimo = 'precioMinimo&=' + numeral(filtros.minimo).value() + 'valMin;'
    }
    if (filtros.maximo) {
      precioMaximo = 'precioMaximo&=' + numeral(filtros.maximo).value() + 'valMax;'
    }

    const rango_precio = precioMinimo + '' + precioMaximo

    let calificacion_filtro = ''
    if (filtros.calificacion) {
      calificacion_filtro = filtros.calificacion
    }

    let empresa_filtro = ''
    if (filtros.empresa && filtros.empresa.length) {
      empresa_filtro = filtros.empresa.join(',')
    }

    let rubro_filtro = ''
    if (filtros.rubro && filtros.rubro.length) {
      rubro_filtro = filtros.rubro.join(',')
    }

    let categoria_filtro = ''
    if (filtros.categoria && filtros.categoria.length) {
      categoria_filtro = filtros.categoria.join(',')
    }

    rootStore.environment.api
      .productosBuscador(
        Pagina,
        cantidadPorPagina,
        '',
        filtros.texto,
        rango_precio,
        empresa_filtro,
        rubro_filtro,
        categoria_filtro,
        calificacion_filtro,
        filtros.mas_vendidos,
      )
      .then((result) => {
        if (result.kind === 'ok') {
          if (result.data.count === 0) {
            registroEventoFirebase(false)
          } else {
            registroEventoFirebase(true)
          }
          // @ts-ignore
          const cantidadPaginas = Math.ceil(result.data.count / cantidadPorPagina)
          setCantidadPaginas(cantidadPaginas)
          // @ts-ignore
          setCantidad(result.data.count)
          if (AgregarPagina) {
            const productos = [...Productos, ...result.data.results]
            setProductos(productos)
            Content.current && Content.current.scrollToBottom(300)
          } else {
            setProductos(result.data.results)
            Content.current && Content.current.scrollToTop(100)
          }
        } else if (result.kind === 'not-found') {
          setCantidadPaginas(1)
          setCantidad(0)
          setPagina(1)
          rootStore.setProductosInicio([])
          Content.current && Content.current.scrollToTop(100)
        } else {
          addToast('Ocurrió un error, vuelva a intentar en unos momentos', 'error')
        }
        setLoading(false)
      })
  }

  useEffect(() => {
    let currentUrlParams = new URLSearchParams(window.location.search)
    //
    const filtros = {} as any
    //
    const ids_categorias = currentUrlParams.get('c') || ''
    filtros.categoria = ids_categorias ? ids_categorias.split(',').map((x) => +x) : null
    //
    const ids_fabricantes = currentUrlParams.get('f') || ''
    filtros.empresa = ids_fabricantes ? ids_fabricantes.split(',').map((x) => +x) : null
    //
    const ids_rubros = currentUrlParams.get('r') || ''
    filtros.rubro = ids_rubros ? ids_rubros.split(',').map((x) => +x) : null
    //
    filtros.calificacion = currentUrlParams.get('cal') || ''
    //
    const mas_vendidos = currentUrlParams.get('mas')
    filtros.mas_vendidos = mas_vendidos === '1'
    //
    filtros.texto = currentUrlParams.get('s') || ''
    //
    filtros.minimo = currentUrlParams.get('minimo') || ''
    //
    filtros.maximo = currentUrlParams.get('maximo') || ''
    //
    setFiltros(filtros)
    //
    obtenerProductos(false, filtros)
  }, [window.location.search])

  useEffect(() => {
    if (!Ready) {
      return
    }
    obtenerProductos(true)
  }, [Pagina])

  useEffect(() => {
    if (Filtros.categoria && Filtros.categoria.length > 0) {
      const fabricantesFiltrados = rootStore.fabricantesVistaCliente.filter((fab) => {
        const categoriasFabricante = fab.categorias
        const found = categoriasFabricante.some((r) => Filtros.categoria.indexOf(r) >= 0)
        if (found) {
          return fab
        } else {
          return null
        }
      })

      const castFabicantesFiltrados = fabricantesFiltrados.map((fabr) => {
        const fabricante = { ...fabr }
        fabricante.categorias = cast([...fabr.categorias])
        return fabricante
      })

      rootStore.setFabricantesVistaClienteFiltrados(castFabicantesFiltrados)
    } else {
      if (rootStore.fabricantesVistaClienteFiltrados.length !== rootStore.fabricantesVistaCliente.length && Ready) {
        const fabricantes: any[] = []
        rootStore.fabricantesVistaCliente.forEach((fab) => {
          const fabricante = { ...fab }
          fabricante.categorias = cast([...fab.categorias])
          fabricantes.push(fabricante)
        })
        rootStore.setFabricantesVistaClienteFiltrados(fabricantes)
      }
    }
  }, [Filtros.categoria])

  const SubHeaderBusqueda = () => {
    return (
      <div className={'barra-resultados padding-header margen-subheader'}>
        <div className={'container-lg'}>
          <div
            className="d-flex"
            style={{ flex: 1, height: '100%', alignItems: 'center', justifyContent: 'space-between' }}
          >
            <div
              style={{
                flex: 1,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'flex-start',
                gap: Movil ? 0 : 5,
                flexWrap: 'wrap',
                paddingTop: 5,
                paddingBottom: 5,
              }}
            >
              {Loading && <IonSpinner color={'primary'} name="crescent" />}
              {!Loading && <>
                <strong>Resultados de búsqueda {!!Filtros.texto && <span>"{Filtros.texto}"</span>}</strong>
                {Cantidad !== -1 && Cantidad > 0 && (
                  <i>
                    {Cantidad} producto{Cantidad > 1 ? 's' : ''} encontrado{Cantidad > 1 ? 's' : ''}
                  </i>
                )}
              </>}
              {Movil && !Loading && Cantidad < 1 && <i>No se encontraron productos</i>}
            </div>

            <div style={{ height: 'fit-content' }}>
              <a
                onClick={() => {
                  setShowSideMenu(!ShowSideMenu)
                }}
              >
                <img height={20} src={'assets/icon/ajustes.png'} alt={'Ajustes'} />
              </a>
            </div>
          </div>
        </div>
      </div>
    )
  }

  const ListadoProductos = () => {
    return (
      <div className="container-fluid p-0 pt-2 background-white" style={{ flex: 3 }}>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          <div>
            <div className={'col-12'}>
              <div className={'content-container row mx-0'}>
                <div>
                  <div className={'row mt-lg-2 mt-0'}>
                    {!Movil && <div className={'col-1'} />}

                    {!Movil && <div className={'col-1'} />}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className={'row'}>
            {Productos.length > 0 &&
              Productos.map((producto: any, index: number) => (
                <ProductoCard
                  key={producto.id}
                  producto={producto}
                  index={index}
                  containerClass={'col-6 mb-4 ' + (ShowSideMenu ? 'col-lg-4' : 'col-lg-3')}
                />
              )
            )}
            {!Loading && Productos.length < 1 && (
              <div className={'col-12 text-center'}>
                <p>No se encontraron productos</p>
              </div>
            )}
          </div>
        </div>
        {CantidadPaginas > Pagina && (
          <div
            className={'cpointer'}
            style={{ display: 'flex', justifyContent: 'center', color: 'var(--rojo-abi)', marginTop: 10 }}
            onClick={() => {
              if (Loading) {
                return
              }
              if (Pagina !== CantidadPaginas && CantidadPaginas >= Pagina) {
                setPagina(Pagina + 1)
              }
            }}
          >
            <strong>{Loading ? 'Cargando..' : '+ Ver más'}</strong>
          </div>
        )}
        <div className={'col-12 text-center mt-3'}>
          <p>
            <a
              target={'_blank'}
              href="https://wa.me/595981840840/?text=Hola%21%20Me%20gustar%C3%ADa%20sugerir%20el%20siguiente%20producto%3A"
            >
              <WhatsAppIcon style={{ marginRight: 5, marginBottom: 3, fontSize: 30 }} />
              Sugerir producto
            </a>
          </p>
        </div>
      </div>
    )
  }

  const seleccionarCategoriaFiltro = (evt: any) => {
    // @ts-ignore
    const categoriaEvt = evt.target.attributes.getNamedItem('data-categoria')
    let categoriasSeleccionadas = Filtros.categoria ? [...Filtros.categoria] : []
    if (categoriaEvt) {
      const idCategoria = parseInt(categoriaEvt.value)
      if (categoriasSeleccionadas.includes(idCategoria)) {
        const idx = categoriasSeleccionadas.indexOf(idCategoria)
        categoriasSeleccionadas.splice(idx, 1)
      } else {
        categoriasSeleccionadas.push(idCategoria)
      }
      setFiltro('categoria', categoriasSeleccionadas)
    }
    // Actualizar URL
    let currentUrlParams = new URLSearchParams(window.location.search)
    currentUrlParams.set('c', categoriasSeleccionadas.join(','))
    history.push(window.location.pathname + '?' + currentUrlParams.toString())
  }

  const seleccionarRubroFiltro = (evt: any) => {
    // @ts-ignore
    const rubroEvt = evt.target.attributes.getNamedItem('data-rubro')
    let rubrosSeleccionados = Filtros.rubro ? [...Filtros.rubro] : []
    if (rubroEvt) {
      const idRubro = parseInt(rubroEvt.value)
      if (rubrosSeleccionados.includes(idRubro)) {
        const idx = rubrosSeleccionados.indexOf(idRubro)
        rubrosSeleccionados.splice(idx, 1)
      } else {
        rubrosSeleccionados.push(idRubro)
      }
      setFiltro('rubro', rubrosSeleccionados)
    }
    // Actualizar URL
    let currentUrlParams = new URLSearchParams(window.location.search)
    currentUrlParams.set('r', rubrosSeleccionados.join(','))
    history.push(window.location.pathname + '?' + currentUrlParams.toString())
  }

  const seleccionarFabricanteFiltro = (evt: any) => {
    // @ts-ignore
    const fabricanteEvt = evt.target.attributes.getNamedItem('data-fabricante')
    let fabricantesSeleccionados = Filtros.empresa ? [...Filtros.empresa] : []
    if (fabricanteEvt) {
      const idEmpresa = parseInt(fabricanteEvt.value)
      if (fabricantesSeleccionados.includes(idEmpresa)) {
        const idx = fabricantesSeleccionados.indexOf(idEmpresa)
        fabricantesSeleccionados.splice(idx, 1)
      } else {
        fabricantesSeleccionados.push(idEmpresa)
      }
      setFiltro('empresa', fabricantesSeleccionados)
      // Actualizar URL
      let currentUrlParams = new URLSearchParams(window.location.search)
      currentUrlParams.set('f', fabricantesSeleccionados.join(','))
      history.push(window.location.pathname + '?' + currentUrlParams.toString())
    }
  }

  const ratingOnChanged = (event: any, newValue: any) => {
    if (typeof newValue === 'number') {
      setFiltro('calificacion', [newValue])
    } else {
      setFiltro('calificacion', [0])
    }
    // Actualizar URL
    let currentUrlParams = new URLSearchParams(window.location.search)
    if (typeof newValue === 'number') {
      currentUrlParams.set('cal', newValue + '')
    } else {
      currentUrlParams.delete('cal')
    }
    history.push(window.location.pathname + '?' + currentUrlParams.toString())
  }

  const renderCheckboxFiltro = (checked: boolean) => {
    // Función utilizada para solucionar problema de checkbox que quedaba chequeado por más de que no debería
    if (checked) {
      return <input
        type={'checkbox'}
        readOnly
        checked={true}
      />
    }
    return <input
      type={'checkbox'}
      readOnly
      checked={false}
    />
  }

  return (
    <Page obtener_datos={obtenerProductos} login_required={false} solo_cliente={true}>
      {/* Contenido de la página*/}
      <IonAlert
        isOpen={AlertBloqueo}
        onDidDismiss={() => setAlertBloqueo(false)}
        header={'Producto restringido'}
        message={`Este producto solo puede ser visualizado por personas mayores de 20 años. ${
          !loginStore.isLoggedIn ? 'Si usted es mayor por favor inicie sesión y vuelva a intentarlo' : ''
        }`}
        buttons={
          !loginStore.isLoggedIn
            ? [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
                {
                  text: 'Iniciar sesión',
                  handler: () => history.push(`${RUTAS.ROOT}?login=1`),
                },
              ]
            : [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
              ]
        }
      />

      <SubHeaderBusqueda />

      <div id={'page-busqueda'} className="page-wrapper pt-0 margen-subheader container-lg" style={{ display: 'flex' }}>
        {(!Movil || !ShowSideMenu) && <ListadoProductos />}
        {ShowSideMenu && (
          <div className={'px-2'} style={{ flex: 1, maxWidth: !Movil ? 300 : '100%' }}>
            <div className={'p-3 border-menu'}>
              <div onClick={() => setShowSideMenu(!ShowSideMenu)} style={{ marginBottom: 10 }}>
                <FIcon.XCircle size={20} />
              </div>

              <p>
                <strong>Calificación</strong>
              </p>
              <div className={'calificacion-contenedor'}>
                <StyledRating
                  className={'mb-2'}
                  name="size-large"
                  value={Filtros.calificacion}
                  size="large"
                  onChange={ratingOnChanged}
                />
              </div>
              <p>
                <strong>Precio</strong>
              </p>
              <div className={'form-group input-group'} style={{gap: 10}}>
                <input
                  placeholder={'Mínimo'}
                  type={'text'}
                  className="form-control"
                  value={Filtros.minimo !== '' ? numeral(Filtros.minimo).format('0,0') : ''}
                  onChange={(evt) => {
                    setFiltro('minimo', evt.target.value)
                  }}
                />
                <input
                  placeholder={'Máximo'}
                  type={'text'}
                  className="form-control"
                  value={Filtros.maximo !== '' ? numeral(Filtros.maximo).format('0,0') : ''}
                  onChange={(evt) => {
                    setFiltro('maximo', evt.target.value)
                  }}
                />
                <a className={'text-left'} style={{ cursor: 'pointer' }} onClick={() => {
                  // Actualizar URL
                  let currentUrlParams = new URLSearchParams(window.location.search)
                  currentUrlParams.set('minimo', numeral(Filtros.minimo).value() || '')
                  currentUrlParams.set('maximo', numeral(Filtros.maximo).value() || '')
                  history.push(window.location.pathname + '?' + currentUrlParams.toString())
                }}>
                  <FIcon.Search size={20} style={{ marginTop: 10 }} />
                </a>
              </div>
              <div className={'form-group'}>
                <input
                  type={'checkbox'}
                  readOnly
                  checked={Filtros.mas_vendidos}
                />
                <label onClick={(evt: any) => {
                  setFiltro('mas_vendidos', !Filtros.mas_vendidos)
                  // Actualizar URL
                  let currentUrlParams = new URLSearchParams(window.location.search)
                  currentUrlParams.set('mas', !Filtros.mas_vendidos ? '1' : '')
                  history.push(window.location.pathname + '?' + currentUrlParams.toString())
                }}>Más vendidos</label>
              </div>
              <p>
                <strong>Proveedores</strong>
              </p>
              {rootStore.fabricantesVistaClienteFiltrados.map((fabricante) => {
                return (
                  <div
                    key={fabricante.id}
                    className={'form-group'}
                    data-fabricante={fabricante.id}
                    onClick={(evt) => {
                      seleccionarFabricanteFiltro(evt)
                    }}
                  >
                    {renderCheckboxFiltro(Filtros.empresa && Filtros.empresa.includes(fabricante.id))}
                    <label
                      data-fabricante={fabricante.id}
                      onClick={(evt) => {
                        seleccionarFabricanteFiltro(evt)
                      }}
                    >
                      {fabricante.nombre}
                    </label>
                  </div>
                )
              })}
              <p>
                <strong>Categorías</strong>
              </p>
              {rootStore.arbolFlat.map((categoria) => {
                return (
                  <div
                    key={categoria.id}
                    data-categoria={categoria.id}
                    onClick={(evt) => {
                      seleccionarCategoriaFiltro(evt)
                    }}
                    className={'form-group'}
                  >
                    {renderCheckboxFiltro(Filtros.categoria && Filtros.categoria.includes(categoria.id))}
                    <label
                      data-categoria={categoria.id}
                      onClick={(evt) => {
                        seleccionarCategoriaFiltro(evt)
                      }}
                    >
                      {categoria.nombre}
                    </label>
                  </div>
                )
              })}
              <p>
                <strong>Rubros</strong>
              </p>
              {rootStore.rubros.map((rubro) => {
                return (
                  <div
                    key={rubro.id}
                    className={'form-group'}
                    data-rubro={rubro.id}
                    onClick={(evt) => {
                      seleccionarRubroFiltro(evt)
                    }}
                  >
                    {renderCheckboxFiltro(Filtros.rubro && Filtros.rubro.includes(rubro.id))}
                    <label
                      data-rubro={rubro.id}
                      onClick={(evt) => {
                        seleccionarRubroFiltro(evt)
                      }}
                    >
                      {rubro.nombre}
                    </label>
                  </div>
                )
              })}
            </div>
          </div>
        )}
      </div>
    </Page>
  )
})

export default Busqueda
